.pagination {
	width: 100%;
	display: flex;
	justify-content: center;
	list-style-type: none;
	padding-left: 0;
	li {
		margin-right: 1.5rem;
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 35px;
			height: 35px;
			font-family: Lato-Regular;
			font-size: 25px;
			text-decoration: none;
			transition: all .2s cubic-bezier(.2, .5, .5, .8);
			color: map-get($color, main-8);
		}
		&:hover {
			a {
				transition: all .2s cubic-bezier(.2, .5, .5, .8);
				font-family: Lato-Bold;
				color: map-get($color, main-4);
			}
		}
		&.active {
			a {
				font-family: Lato-Bold;
				text-decoration: none;
				color: map-get($color, main-4);
			}
		}
		&.pagination-prev,
		&.pagination-next  {
			background:rgba($color: #DCC375, $alpha: .77);
			position: relative;
			&::before {
				content: "";
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
				width: 6px;
				height: 11px;
				background-size: 100% 100%;
			}
			&.disable {
				pointer-events: none;
				background: rgba($color: #DCC375, $alpha: .48);
			}
		}
		&.pagination-prev {
			border-radius: 7px 0 0 7px;
			&::before {
				background: url(../assets/img/prev.png) no-repeat;
			}
		}	
		&.pagination-next {
			border-radius: 0px 7px 7px 0px;
			&::before {
				background: url(../assets/img/next.png) no-repeat;
			}
		}
		@media (max-width: 575.98px) {
			margin: 0;
		}
	}
	@media (max-width: 1024.98px) {
		li {
			a {
				font-size: 14px;
			}
		}
	}
}

