#quiz-answer {
	width: 100%;
	background: url(../assets/img/bg-popup-rule.png) no-repeat;
	background-size: 100% 100%;
	position: relative;
	overflow: hidden;
	height: 100%;

	@media (max-width: 1300.98px) {
		overflow-y: auto;
	}
	p,
	label,
	span {
		color: map-get($color, main-8);
		font-size: 2.5rem;
		@media (max-width: 1024.98px) {
			font-size: 1.8rem;
		}
	}
	.main-title-img {
		margin-bottom: 60px;
		@media (max-width: 1024.98px) {
			margin-bottom: 20px;
		}
	}
	.quiz-content {
		display: grid;
		grid-template-rows: min-content 25% min-content;  // based on slidesPerColumn
		grid-auto-flow: column;
		grid-gap: 20px;
		@media (max-width: 1200.98px) {
			grid-template-rows: unset;  // based on slidesPerColumn	
			grid-template-columns: auto;
			grid-auto-flow: unset;
			grid-gap: 5px;
		}
	}
	.item {
		&:last-child {
			display: flex;
			justify-content: flex-end;
			align-items: flex-end;
			.playgame__wrapper {
				&::after {
					content: none;
				}
			}
			.btn-playgame {
				width: 156px;
				height: 60px;
				font-size: 2rem;
				font-family: Lato-Bold;
				&::after,
				&::before {
					content: none;
				}
				@media (max-width: 1024.98px) {
					width: 100px;
					height: 40px;
					font-size: 1.6rem;
				}
			}
		}
		input[type=radio] {
			position: relative;
			&:checked {
				&::after {
					position: absolute;
					content: "";
					background: map-get($color, main-4);
					width: 100%;
					height: 100%;
					border-radius: 50%;
				}

			}
			
		}
	}
	.question {
		margin-bottom: 20px;
		display: flex;
		@media (max-width: 1024.98px) {
			margin-bottom: 5px;
		}
		.question-item {
