	}
	.answer--item {
		margin-bottom: 20px;
		padding-left: 135px;
		@media (max-width: 1024.98px) {
			margin-bottom: 5px;
			padding-left: 14%;
		}
		.wrapper {
			display: inline-flex;
			align-items: center;
			transition: .5 all ease-in-out;
			input {
				transition: .5s all ease-in-out;
				width: 19px;
				height: 19px;
				-ms-flex: 0 0 19px;
				flex: 0 0 19px;
				max-width: 19px;
			}
		}
		label {
			padding-left: 30px;
			transition: .5s all ease-in-out;
		}
		&.active {
			.wrapper {
				margin-right: -10px;
				margin-left: -10px;
				padding: 0 10px;
				border-radius: 17px;
				border: 1px solid map-get($color, main-4);
				transition: .5s all ease-in-out;
			}
			label {
				color: map-get($color, main-4);
				transition: .5s all ease-in-out;
			}
			input[type=radio] {
				position: relative;
				&::after {
					background: url(../assets/img/check.png) no-repeat !important;
					content: "";
					position: absolute;
					background-size: 100% 100%;
					top: 50%;
					left: 50%;
					width: 100%;
					height: 100%;
					object-fit: cover;
					transform: translate(-50%,-50%);
				}
			}
		}
		&.active-hint {
			label {
				transition: .5s all ease-in-out;
				color: map-get($color, main-4);
			}
			input[type=radio] {
				position: relative;
				&::after {
					transition: .5s all ease-in-out;
					content: "";
					position: absolute;
					background: map-get($color, main-4) no-repeat;
					border-radius: 50%;
					background-size: 100% 100%;
					top: 50%;
					left: 50%;
					width: 100%;
					height: 100%;
					transform: translate(-50%,-50%);
				}
			}
		}
		&.disable {
			pointer-events: none;
			label {
				color: #F60000;
			}
			input[type=radio] {
				position: relative;
				&::after {
					content: "";
					position: absolute;
					background: url(../assets/img/false.png) no-repeat;
					background-size: 100% 100%;
					top: 50%;
					left: 50%;
					width: 100%;
					height: 100%;
					transform: translate(-50%,-50%);
				}
			}
		}
	}
}
